



body {
   background: #0f0c29;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #24243e, #302b63, #0f0c29);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #24243e, #302b63, #0f0c29); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
 

    display: flex;
    justify-content: center;
 }
 .container {
    display: block;
    width: 500px;
    margin-top: 120px;
 }
 .title {
    text-align: center;
    color: white;
 }
 .new-task {
    display: block;
    width: 100%;
    background-color: azure;
    border-radius: 10px;
    height: 40px;
    border-color: aquamarine;
    border-width: 4px;
    text-align: center;
    font-size: 24px;
 }
 .test-label {
   text-align: center;
   color: white;
}
.task-container {
   width: 100%;
   background-color: azure;
   margin-bottom: 8px;
   font-size: 16px;
   border-left: 8px solid aqua !important;
 }
 .task {
   margin: 0;
   padding: 10px 0px 10px 10px;
 }
 .delete-button {
   background-color: black;
   border-radius: 13px;
   height: 23px;
   color: white;
   border-width: 0px;
   font-weight: bolder;
 }
 .button-container {
   position: relative;
   float: right;
   margin: -32px 10px;
 }
